
*{
  margin: 0;
  padding: 0;
}
html,body{
  width: 100%;
  height: 100%;
}
.fl{
  float: left;
}
.fr{
  float: right;
}
a{
  text-decoration: none;
}
.cf::after{
  display: table;
  content: '';
  clear: both;
}
#app {
  width: 100%;
  top: 0;
  left: 0;
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  height: 100%;
}

#footer{
  position: fixed;
  border-top: 3px solid #eee;
  z-index: 5;
  bottom: 0;
  left: 0;
  background-color: #adf4f4;
  width:100%;
  font-size: 2rem;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
#footer .f-btn{
  -webkit-box-flex: 1;
      -ms-flex: auto;
          flex: auto;
}
#footer .router-link-active{
  background-color: #f4e25a;
}

#index{
  background-color: #ebe5e5e8;
  padding-bottom: 4rem;
}
#index .notice{
  background-color: #ceddd9;
  margin-bottom: 10px;
}
#index .notice .sc{
  background-color: #f4e25a;
}
#index .notice ul{
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
#index .notice ul li{
  padding: 0.5rem;
  list-style: none;
  font-size: 2rem;
  line-height: 2rem;
  -webkit-box-flex: 1;
      -ms-flex: auto;
          flex: auto;
}

#postinfo{
  text-align: left;
  /* background-color: pink; */
}
#postinfo .infolist{
  margin-bottom: 10px;
  background-color: #fff;
}
#postinfo .p-head{
  height: 50px;
  border-top: 1px solid #f9eeb1;
  /* background-color: pink; */
}
#postinfo .p-h-icon{
  width: 50px;
  height: 50px;
  margin-right: 10px;
  border-radius: 50%;
}
#postinfo .p-h-icon img{
  width: 100%;
  height: 100%;
  border-radius: 50%;
}
#postinfo .p-h-user{
  width: auto;
  height: 50px;
}
#postinfo .p-h-delbtn{
  padding: 10px;
  font-size: 20px;
  line-height: 20px;
  height: 20px;
  border-radius: 8px;
  margin-top: 5px;
  background-color: red;
  color: white;
}
#postinfo .p-h-id{
  padding-top: 5px;
  line-height: 20px;
  font-size: 20px;
  font-weight: bold;
  color: rgb(85, 117, 206);
}
#postinfo .p-h-time{
  padding: 7px 0 3px 0;
  line-height: 15px;
  font-size: 15px;
  color: rgb(206, 192, 192);
  vertical-align: middle;
}
#postinfo .p-body{
  margin-top: 5px;
  background-color: rgba(196, 242, 242,.2);
  border-top: 1px solid gray;
  border-bottom: 1px solid #737a6b;
}
#postinfo .p-b-title{
  line-height: 2rem;
  font-size: 2rem;
  font-weight: bold;
}
#postinfo .p-b-subtitle{
  line-height: 1.5rem;
  font-size: 1.5rem;
  font-weight: 600;
  margin: 0.5rem 0;
  padding: 0.3rem 0;
  border-top: 1px solid #dbfcae;
  border-bottom: 1px solid #dbfcae;
}
#postinfo .p-b-content pre{
  white-space: pre-wrap;
  white-space: -moz-pre-wrap;
  white-space: -pre-wrap;
  white-space: -o-pre-wrap;
  word-wrap: break-word;
  white-space : normal;
  font-size: 1.4rem;
  line-height: 1.4rem;
  min-height: 3rem;
}
#postinfo .p-foot{
  margin-top: 5px;
  padding: 5px 0;
  /* background-color: pink; */
}
#postinfo .p-foot .p-f-content{
  border: none;
  border-top: 1px solid gray;
  border-bottom: 1px solid gray;
  outline: none;
  resize: none;
  font-size: 1.5rem;
  line-height: 1.5rem;
  width:100%;
  overflow:auto;
  word-break:break-all;
  background-color: rgb(191, 236, 230);
}
#postinfo .p-foot ul{
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
#postinfo .p-foot ul li{
  text-align: center;
  list-style: none;
  -webkit-box-flex: 1;
      -ms-flex: auto;
          flex: auto;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
#postinfo .p-foot .submit{
  font-size: 1.5rem;
  padding: 5px 15px;
  border-radius: 8px;
  background-color: rgb(85, 214, 85);
}
#postinfo .p-review{
  max-height: 20rem;
  overflow: auto;
}
#postinfo .p-review .p-r-list{
  margin: 10px 0 10px 20px;
  padding: 5px 5%;
  border-top: 1px solid gray;
}
#postinfo .p-review .p-r-user{
  width: 15%;
  padding-right: 1rem;
  font-size: 1.2rem;
  word-wrap:break-word;
  word-break:break-all;
}
#postinfo .p-review .p-r-c{
  text-align: left;
  width: 75%;
  font-size: 1.2rem;
  word-wrap:break-word;
  word-break:break-all;
}

#writepost{
  text-align: left;
  position: relative;
  top:0;
  left: 0;
  height: 91%; /* Safari 5.1 - 6.0 */ /* Opera 11.1 - 12.0 */ /* Firefox 3.6 - 15 */
  background: -webkit-gradient(linear, left top, left bottom, from(#cdf7f5), to(#baddf2));
  background: linear-gradient(#cdf7f5, #baddf2); /* 标准的语法（必须放在最后） */
}
#writepost .title{
  font-size: 2rem;
  font-weight: bold;
  text-align: center;
}
#writepost .infolist{
  padding: 10px 3px 30px 3px;
  /* background-color: pink; */
}
#writepost .i-body{
  width: 100%;
  /* background-color: yellow; */
}
#writepost .i-body .ibi{
  width: 100%;
  margin-bottom: 1rem;
  min-height: 1.5rem;
  font-size: 1.2rem;
  line-height: 1.2rem;
  padding: 0.2rem 0;
  border: none;
  border-top: 1px solid gray;
  outline: none;
}
#writepost .i-body .ib-content{
  border: none;
  border-top: 1px solid gray;
  border-bottom: 1px solid gray;
  outline: none;
  resize: none;
  font-size: 1.5rem;
  line-height: 1.5rem;
  width:100%;
  overflow:auto;
  word-break:break-all;
}
#writepost .i-body .i-b-prop{
  font-size: 1.5rem;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
#writepost .i-body .i-b-prop div{
  text-align: center;
  float: left;
  -webkit-box-flex: 1;
      -ms-flex: auto;
          flex: auto;
}
#writepost .i-body .i-b-prop input{
  width: 1.2rem;
  height: 1.2rem;
}
#writepost .i-body .i-b-avt{
  height: 2rem;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
#writepost .i-body .i-b-avt div{
  text-align: center;
  -webkit-box-flex: 1;
      -ms-flex: auto;
          flex: auto;
  float: left;
}
#writepost .i-body .i-b-avt img{
  height: 100%;
  height: 2rem;
}
#writepost .foot{
  position: absolute;
  bottom: 1rem;
  width: 100%;
  text-align: center;
}
#writepost .foot .f-info{
  padding: 5px 0;
  font-size: 2rem;
  line-height: 2rem;
  color: red;
}
#writepost .foot .postbtn{
  width: 80%;
  font-size: 2rem;
  line-height: 2rem;
  padding: 5px;
  background-color: skyblue;
  border: 1px solid gray;
  border-radius: 1rem;
}

#info{
  position: relative;
  height: 92%; /* Safari 5.1 - 6.0 */ /* Opera 11.1 - 12.0 */ /* Firefox 3.6 - 15 */
  background: -webkit-gradient(linear, left top, left bottom, from(#4fafea), to(#baddf2));
  background: linear-gradient(#4fafea, #baddf2); /* 标准的语法（必须放在最后） */
  overflow: scroll;
}
#info button.delbtn{
  color: white;
  padding: 2px 9px;
  background-color: red;
  border-radius: 10px;
  font-size: 1.5rem;
}
#info .loginfo{
  padding: 30px 15px;
  text-align: left;
}
#info .loginfo .u-info{
  min-width: 60%;
  float: left;
}
#info .loginfo span.u-id{
  height: 30px;
  display: block;
  font-size: 2rem;
  font-weight: bold;
}
#info .loginfo span.u-role{
  height: 20px;
  display: block;
  font-size: 1rem;
}
#info .loginfo button{
  height: 50px;
  display: inline-block;
  font-size: 2rem;
  border-radius: 8px;
  border: 1px solid red;
  color: white;
  background-color: red;
  padding: 3px 20px;
}
#info .umbtn{
  width: 96%;
  font-size: 1.5rem;
  border-radius: 10px;
}
#info .usermg .user{
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 80%;
  margin: 0 auto;
  padding: 8px;
  font-size: 1.5rem;
}
#info .usermg .user>span{
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
#info .usermg .user>select{
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
#info .usermg .user>button{
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
#info .post{
  width: 94%;
  margin: 0 auto;
  padding: 2.6rem 10px 0 10px;
  border-bottom: 1px solid gray;
  font-size: 1.5rem;
  position: relative;
}
#info .post>span{
  position: absolute;
  top: 10px;
  left: 2rem;
}
#info .post>button{
  position: absolute;
  top: 10px;
  font-size: 1.2rem;
  border-radius: 8px;
  right: 2rem;
}
#info .post>p{
  text-align: left;
  font-size: 1rem;
}

